import React from 'react';
import ReactDOM from 'react-dom/client';
import { HelmetProvider } from 'react-helmet-async';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import {
  initPerformanceMonitoring,
  checkPerformanceBudget,
} from './utils/webVitals';
import { initializeCacheOptimization } from './utils/cacheOptimization';
import logger from './utils/logger';

const rootElement = document.getElementById('root');

if (!rootElement) {
  throw new Error('Failed to find the root element');
}

const root = ReactDOM.createRoot(rootElement);

root.render(
  <React.StrictMode>
    <HelmetProvider>
      <App />
    </HelmetProvider>
  </React.StrictMode>
);

// Service Worker -   
if (process.env.NODE_ENV === 'production') {
  serviceWorkerRegistration.register({
    onUpdate: (registration: ServiceWorkerRegistration) => {
      //     
      // AppUpdateNotification  
      //      
      logger.info('New service worker available:', registration);
      //     
      if (registration.waiting) {
        registration.waiting.postMessage({ type: 'SKIP_WAITING' });
      }
    },
    onSuccess: (_registration: ServiceWorkerRegistration) => {
      logger.info('Service Worker registered successfully');
    },
  });
} else {
  //    Service Worker  
  const unregisterPromise = serviceWorkerRegistration.unregister();
  if (unregisterPromise && typeof unregisterPromise.then === 'function') {
    unregisterPromise
      .then(() => {
        //   
        if ('caches' in window) {
          caches.keys().then((names) => {
            names.forEach((name) => {
              caches.delete(name);
            });
          });
        }
      })
      .catch((error) => {
        logger.warn('Failed to unregister service worker:', error);
      });
  }
}

// Initialize cache optimization
initializeCacheOptimization();

// Initialize enhanced performance monitoring
initPerformanceMonitoring({
  enableLogging: process.env.NODE_ENV === 'development',
  sampleRate: process.env.NODE_ENV === 'production' ? 0.1 : 1, // 10% sampling in production
});

// Check performance budgets
if (process.env.NODE_ENV === 'development') {
  checkPerformanceBudget();
}
